import React from "react";
import { NavLink } from "react-router-dom";
import { removeToken } from "../../utils/token";

function HomeComponent(props) {
	const { history } = props;
	return (
		<div>
			<p>
				<NavLink to="/book" exact>
					
				</NavLink>
			</p>
			<p>
				<NavLink to="/user" exact>
					
				</NavLink>
			</p>
			<p>
				<button
					onClick={() => {
						removeToken();
						history.push("/login");
					}}
				>
					
				</button>
			</p>
		</div>
	);
}

export default HomeComponent;
