import React from "react";
import { setToken } from "../../utils/token";

function LoginComponent(props) {
	const { history } = props;
	return (
		<div>
			<h1>Login Page</h1>
			<button
				onClick={() => {
					const LoginInfo = {
						value: "chenjiang",
						level: 0, // 
					};
					setToken(JSON.stringify(LoginInfo));
					history.push("/");
				}}
			>
				
			</button>
			<button
				onClick={() => {
					const LoginInfo = {
						value: "chenjiang",
						level: 1, // 
					};
					setToken(JSON.stringify(LoginInfo));
					history.push("/");
				}}
			>
				
			</button>
		</div>
	);
}

export default LoginComponent;
