import { HashRouter, Redirect, Route, Switch } from "react-router-dom";
import routerConfig from "./config";
import { getToken } from "../../utils/token";

const PrivateRoute = (props) => {
	const { routerConfig, location } = props;
	const { pathname } = location;
	const isLogin = JSON.parse(getToken()); // 
	const currentRoute = routerConfig.find((item) => item.path === pathname); // 

	/* if eles  */
	if (isLogin) {
		/*  */
		if (pathname === "/login") {
			return <Redirect to={"/"} />;
		} else {
			if (currentRoute) {
				/*  */
				if (currentRoute.super && isLogin.level <= 0) {
					// 
					return <Redirect to={"/noPermission"} />;
				}
				return <Route path={pathname} component={currentRoute.component} />;
			} else {
				/*  */
				return <Redirect to={"/404"} />;
			}
		}
	} else {
		/*  */
		if (currentRoute) {
			if (currentRoute.auth) {
				/*  */
				return <Redirect to={"/login"} />;
			} else {
				/*    */
				return <Route path={pathname} component={currentRoute.component} />;
			}
		} else {
			/*  404 */
			return <Redirect to={"/404"} />;
		}
	}
};

function MainRouter(props) {
	return (
		<div>
			<HashRouter>
				<Switch>
					<PrivateRoute routerConfig={routerConfig} />
					{/* {routerConfig.map(({ path, component: Component }) => {
						return <Route key={path} path={path} component={Component} />;
					})} */}
				</Switch>
			</HashRouter>
		</div>
	);
}

export default MainRouter;
